.about_container{
  padding-top: 2.5rem;
}

.about_title {
  font-size: 70px;
  margin-bottom:2rem;
}

.about_content_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 50px;
}

.about_img_container {
  flex: 1;
  max-width: 500px;
  /* margin-right: 50px; */
  margin-bottom: 0;
}

.about_img {
  width: 350px;
  height: 350px;
  border-radius: 1.5rem;
  justify-content: center;
}

.about_content {
  flex: 1;
  text-align: left;
}

/* .about_description_container {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
} */

.about_description {
  font-size: 1.2rem;
  margin-bottom: 1.2rem;
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
}

.about_button_container {
  max-width: 500px;
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.about_button {
  width: 490px;
}





@media screen and (max-width: 960px) {

  .about_title {
    font-size: 28px;
    text-align: center;
  }

  .about_content_container {
    flex-direction: column;
    margin: 0px;
  }

  
  .about_img_container {
    margin-right:0px;
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
  }
  
  .about_content {
    width: 100%;
    text-align: center;
  }

  .about_img {
    width: 60%;
    height: auto;
    border-radius: 1.5rem;
  }

  .about_description {
    font-size: 12px;
    margin-bottom: 1.1rem;
    line-height: 1.5;
  }
  
.about_button_container {
  max-width: 300px;
}

.about_button {
  font-size: 1rem;
}



}





